<template>
  <div>
    <el-dialog :title="title" :visible.sync="dialog_status" :before-close="closeDialog" width="30%">
      <el-form :model="form" ref="form" :rules="rule">
        <el-form-item prop="title" label="Title" :label-width="label_width">
          <el-input v-model="form.title" class="input"></el-input>
        </el-form-item>
        <el-form-item prop="description" label="Description" :label-width="label_width">
          <el-input v-model="form.description" class="input"></el-input>
        </el-form-item>
      </el-form>
      <el-form>
        <el-form-item prop="rules" label="Rules" :label-width="label_width">
          <div class="tree_box">
            <el-tree
              :data="data"
              :props="defaultProps"
              highlightCurrent
              defaultExpandAll
              showCheckbox
              nodeKey="id"
              ref="tree"
              accordion>
            </el-tree>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button type="primary" @click="addGroup">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<style scoped>
  .input {
    width: 80%;
  }

  .tree_box {
    height: 30%;
    width: 80%;
  }
</style>


<script>
  import { apiPost } from '../../static/js/common.js'

  export default {
    data () {
      let title = (rule, value, callback) => {
        if (!value) {
          this.message.topWarning(this, '请填写权限组名称')
          callback(new Error(' '))
        }
        callback()
      }
      return {
        title: 'Add AuthGroup',
        dialog_status: false,
        form: {
          id: '',
          title: '',
          description: '',
          rules: ''
        },
        data: [],
        defaultProps: {
          children: 'second',
          label: 'first'
        },
        label_width: '120px',
        rule: {
          title: [
            {validator: title, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      openAdd () {
        this.dialog_status = true
        if (this.form) {
          let keys = this.form.rules.split(',')
          setTimeout(() => {
            this.$refs.tree.setCheckedKeys(keys)
          }, 300)
        }
      },
      closeDialog () {
        this.$refs['form'].resetFields()
        this.$refs.tree.setCheckedKeys([])
        this.form.rules = ''
        this.dialog_status = false
      },
      addGroup () {
        let that = this
        let rules = this.$refs.tree.getCheckedKeys()
        if (!rules) {
          return this.message.topWarning(this, '请选择权限')
        } else {
          this.form.rules = rules.join(',')
        }
        if (!this.form.title) {
          return this.message.topWarning(this, '请填写权限组名称！')
        }
        let load = this.message.load(this, 'Saving...')
        let dialog = {id: 'dialog_status', action: false}
        apiPost(that, that.form, '/AuthGroup/add', load, dialog).then(function (res) {
          if (res.code === 200) {
            that.$emit('refreshTable')
          }
        })
        setTimeout(() => {
          this.$refs['form'].resetFields()
        }, 3000)
      }
    }
  }
</script>



