import Vue from 'vue'
import Router from 'vue-router'
import index from '@/Index/index'
import login from '@/Login/index'
import register from '@/Login/register'
import User from '@/User/index'
import AuthRule from '@/AuthRule/index'
import AuthGroup from '@/AuthGroup/index'

Vue.use(Router)

export default new Router({
  routes: [
    // 登录页面
    {
      path: '/login',
      name: 'login',
      component: login
    },
    // 注册页面
    {
      path: '/register',
      name: 'register',
      component: register
    },
    // 默认首页
    {
      path: '/',
      name: 'Home',
      component: index
    },
    /**
     * System 模块
     */
    // 用户页面
    {
      path: '/User',
      name: 'User',
      component: User
    },
    // 权限页面
    {
      path: '/AuthRule',
      name: 'AuthRule',
      component: AuthRule
    },
    // 权限组页面
    {
      path: '/AuthGroup',
      name: 'AuthGroup',
      component: AuthGroup
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



