<template>
  <div>
    <el-row>
      <el-col :span="4" v-for="(item, index) in data" :key="item.id" :offset="1">
        <el-card>
          <img :src="item.head_img" class="image">
          <div style="padding: 10px;">
            <span v-html="item.name"></span>
            <div class="clearfix" style="width: 220px">
              <!--<time class="time" v-html="item.created_at"></time>-->
              <el-button type="default" class="button" style="margin-top: 10px; margin-left: -15px; float: left" size="mini" icon="el-icon-search"></el-button>
              <el-button type="default" class="button" style="margin-top: 10px; float: left" size="mini" @click="editUser(item.id)"><i style="color: #409EFF"
                                                                                                                                       class="el-icon-edit"></i>
              </el-button>
              <el-button type="default" class="button" style="margin-top: 10px; float: left" size="mini"><i style="color: #d9534f" class="fas fa-stop"></i>
              </el-button>
              <el-button type="default" class="button" style="margin-top: 10px; float: left" size="mini"><i class="far fa-trash-alt"></i></el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <editUser ref="editUser" @refreshUsers="getUsers"></editUser>
  </div>
</template>

<style scoped>
  .time {
    font-size: 13px;
    color: #999;
  }

  .image {
    width: 200px;
    border-radius: 50%;
  }

  .button {
    float: right;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }
</style>

<script>
  import editUser from './edit'
  import { apiPost } from '../../static/js/common.js'

  export default {
    data () {
      return {
        data: ''
      }
    },
    created () {
      this.getUsers()
    },
    methods: {
      getUsers () {
        let that = this
        let load = this.message.load(this)
        apiPost(that, '', '/User/index', load, false, false).then(function (res) {
          that.data = res.data.list
          for (let item of that.data) {
            item.groupList = []
            for (let group of item.groups) {
              item.groupList.push(group.group_id)
            }
          }
        })
      },
      editUser (id) {
        for (let item of this.data) {
          if (item.id === id) {
            this.$refs.editUser.form = {id: item.id, name: item.name, email: item.email, head_img: item.head_img}
            this.$refs.editUser.groupList = item.groupList
          }
        }
        this.$refs.editUser.openDialog()
      }
    },
    components: {
      editUser
    }
  }
</script>



