<template>
  <div>
    <el-dialog title="Add AuthRule" :visible.sync="dialog_status" :before-close="closeDialog" width="30%">
      <el-form :model="form" ref="form" :rules="rule">
        <el-form-item prop="rule_id" label="Parent" :label-width="label_width">
          <el-select v-model="form.rule_id" filterable placeholder="请选择" class="input">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="type" label="Type" :label-width="label_width">
          <el-select v-model="form.type" placeholder="请选择菜单类型" class="input">
            <el-option label="前端菜单" value="1"></el-option>
            <el-option label="后端菜单" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="menu_type" label="MenuType" :label-width="label_width">
          <el-select v-model="form.menu_type" placeholder="请选择菜单类型" class="input">
            <el-option label="顶级菜单" value="1"></el-option>
            <el-option label="二级菜单" value="2"></el-option>
            <el-option label="方法" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="title" label="Title" :label-width="label_width">
          <el-input v-model="form.title" class="input"></el-input>
        </el-form-item>
        <el-form-item prop="description" label="Description" :label-width="label_width">
          <el-input v-model="form.description" class="input"></el-input>
        </el-form-item>
        <el-form-item prop="icon" label="Icon" :label-width="label_width" style="position: relative">
          <el-input v-model="form.icon" class="input"></el-input>
          <i :class="form.icon" class="icon-preview"></i>
        </el-form-item>
        <el-form-item prop="name" label="Rule" :label-width="label_width">
          <el-input v-model="form.name" class="input"></el-input>
        </el-form-item>
        <el-form-item prop="sort" label="Sort" :label-width="label_width">
          <el-input v-model="form.sort" class="input"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button type="primary" @click="addRule">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<style>
  .input {
    width: 80%;
  }

  .icon-preview {
    font-size: 28px;
    position: relative;
    top: 6px;
    right: 12%;
    color: #909399;
  }
</style>


<script>
  import { apiPost } from '../../static/js/common.js'

  export default {
    data () {
      let title = (rule, value, callback) => {
        if (!value) {
          this.message.topWarning(this, '请填写规则参数')
          callback(new Error(' '))
        }
        callback()
      }
      let description = (rule, value, callback) => {
        if (!value) {
          this.message.topWarning(this, '请填写规则描述')
          callback(new Error(' '))
        }
        callback()
      }
      let name = (rule, value, callback) => {
        if (!value) {
          this.message.topWarning(this, '请填写规则参数')
          callback(new Error(' '))
        }
        callback()
      }
      let password = (rule, value, callback) => {
        if (!value) {
          this.message.topWarning(this, '密码不能为空')
          callback(new Error(' '))
        }
        callback()
      }
      return {
        dialog_status: false,
        form: {
          id: '',
          rule_id: '',
          menu_type: '1',
          title: '',
          description: '',
          icon: '',
          type: '2',
          name: '',
          sort: 1
        },
        label_width: '120px',
        options: [
          {label: 'Top Menu', value: 0}
        ],
        rule: {
          title: [
            {validator: title, trigger: 'blur'}
          ],
          description: [
            {validator: description, trigger: 'blur'}
          ],
          name: [
            {validator: name, trigger: 'blur'}
          ],
          password: [
            {validator: password, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      openAdd () {
        let that = this
        let dialog = {id: 'dialog_status', action: true}
        let load = this.message.load(this)
        this.options = [{label: 'Top Menu', value: 0}]
        apiPost(that, [], '/AuthRule/getTopRule', load, dialog, false).then(function (res) {
          let data = res.data
          for (let item in data) {
            let items = {label: data[item]['title'], value: data[item]['id']}
            that.options.push(items)
          }
        })
      },
      closeDialog () {
        this.$refs['form'].resetFields()
        this.dialog_status = false
      },
      addRule () {
        let load = this.message.load(this, 'Saving...')
        let that = this
        let dialog = {id: 'dialog_status', action: false}
        that.common.apiPost(that, that.form, '/AuthRule/add', load, dialog).then(function (res) {
          if (res.code === 200) {
            that.$refs['form'].resetFields()
            that.$emit('refreshTree')
          }
        })
      }
    }
  }
</script>



