<template>
  <el-row class="tac sidebar">
    <el-col :span="24">
      <el-menu
        :default-active="$route.path"
        class="el-menu-vertical-demo menu"
        :unique-opened="true"
        router
        background-color="#545c64"
        text-color="#FFF"
        active-text-color="#ffd04b">
        <div class="head_box">
          <div style="width: 57%; float: left">
            <img :src="this.user.head" class="head_img" alt="">
          </div>
          <div class="user_box">
            <span class="user_name" v-html="this.user.name"></span>
            <DropOne class="user_select" :DropOneStyle="DropOneStyle" :DropOneItems="DropOneItems"></DropOne>
          </div>
        </div>
        <br/>
        <el-menu-item :key="this.home.name" :index="this.home.route">
          <i :class="this.home.icon"></i>
          <span slot="title">{{this.home.name}}</span>
        </el-menu-item>
        <SideMenu :menuItems="menuItems"></SideMenu>
      </el-menu>
    </el-col>
  </el-row>
</template>

<style scoped>
  .head_box {
    margin-left: 16%;
    margin-top: 10%;
  }

  .head_box img {
    width: 75px;
    height: 75px;
    border-radius: 50%;
  }

  .user_box {
    padding-top: 10%;
  }

  .user_box .user_name {
    color: antiquewhite;
    font-weight: 200;
    margin-left: -15px;
  }

  .user_box .user_select {
    margin-left: -15px;
  }

  .menu {
    min-height: 953px;
  }

</style>

<script>
  import DropOne from '../Tool/DropOne'
  import SideMenu from './SideMenu'

  export default {
    name: 'Sidebar',
    data: function () {
      return {
        DropOneStyle: 'drop_sidebar',
        DropOneItems: {
          0: {name: 'Profile', isDisabled: true, isDivided: false, href: 'Profile'},
          1: {name: 'Contacts', isDisabled: false, isDivided: false, href: 'Contacts'},
          2: {name: 'Mailbox', isDisabled: false, isDivided: false, href: 'Mailbox'},
          3: {name: 'Login Out', isDisabled: false, isDivided: true, href: 'Login Out'}
        },
        home: {route: '/', name: 'Home', icon: 'el-icon-more'},
        menuItems: [],
        user: {
          name: 'Guest',
          head: '/static/img/head_img/default.png'
        }
      }
    },
    components: {
      DropOne,
      SideMenu
    },
    computed: {
      options () {
        return this.$store.state.options
      },
      activeIndex: {
        get () {
          return this.$store.state.activeIndex
        },
        set (val) {
          this.$store.commit('set_active_index', val)
        }
      }
    },
    mounted () {
      // 刷新时以当前路由做为tab加入tabs
      let flag = true
      for (let option of this.options) {
        if (option.name === 'Home') {
          flag = false
        }
      }
      if (flag) {
        this.$store.commit('add_tabs', {route: '/', name: 'Home'})
        this.$store.commit('set_active_index', '/')
        this.$router.push('/')
      }
    },
    created () {
      this.getSideBar()
    },
    methods: {
      // 获取子级
      getSecond (data) {
        let second = []
        for (let item in data) {
          second.push({first: data[item].first, id: data[item].id})
        }
        return second
      },
      // 获取用户信息
      getUser () {
        let that = this
        let time = this.storage.getExp('token')
        let user = this.storage.get('user')
        if (!user) {
          that.common.apiPost(that, '', '/User/getUser', '', false, false).then(function (res) {
            if (res.code === 200) {
              user = {name: res.data.name, head: res.data.head_img}
              that.user = user
              that.storage.set('user', user, (time - (new Date() - 1)) / 1000)
            }
          })
        }
        that.user.name = user.name
        that.user.head = user.head
      },
      // 获取左侧菜单
      getSideBar () {
        let that = this
        that.common.apiPost(that, '', '/Index/sidebar', '', false, false).then(function (res) {
          let data = []
          let second = []
          if (res.data) {
            for (let item in res.data) {
              let temp = res.data[item]
              if (temp.second) {
                second = that.getSecond(temp.second)
                data.push({first: temp.first, id: temp.id, second: second})
              } else {
                data.push({first: temp.first, id: temp.id})
              }
            }
          }
          that.menuItems = data
          that.getUser()
        })
      }
    },
    watch: {
      '$route' (to) {
        let flag = false
        for (let option of this.options) {
          if (option.name === to.name) {
            flag = true
            this.$store.commit('set_active_index', '/' + to.path.split('/')[1])
            break
          }
        }
        if (!flag) {
          this.$store.commit('add_tabs', {route: '/' + to.path.split('/')[1], name: to.name})
          this.$store.commit('set_active_index', '/' + to.path.split('/')[1])
        }
      }
    }
  }
</script>



