<template>
  <div>
    <el-dialog :title="title" :visible.sync="dialog_status" :before-close="closeDialog" width="30%">
      <el-form :model="form" ref="form">
        <el-form-item prop="head_img" :label-width="label_width">
          <img :src="form.head_img" class="image">
        </el-form-item>
        <el-form-item prop="name" label="Name" :label-width="label_width">
          <el-input v-model="form.name" class="input" disabled></el-input>
        </el-form-item>
        <el-form-item prop="email" label="Email" :label-width="label_width">
          <el-input v-model="form.email" class="input" disabled=""></el-input>
        </el-form-item>
        <el-form-item prop="group" label="Group" :label-width="label_width">
          <el-checkbox-group v-model="groupList">
            <el-checkbox v-for="item in groups " :key="item.id" :label="item.id">{{ item.title }}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button type="primary" @click="saveUser">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<style scoped>
  .input {
    width: 80%;
  }

  .image {
    width: 100px;
    border-radius: 50%;
  }
</style>

<script>
  import { apiPost } from '../../static/js/common.js'

  export default {
    data () {
      let name = (rule, value, callback) => {
        if (!value) {
          this.message.topWarning(this, '用户名称不能为空')
          callback(new Error(' '))
        }
        callback()
      }
      let email = (rule, value, callback) => {
        if (!value) {
          this.message.topWarning(this, '用户名称不能为空')
          callback(new Error(' '))
        }
        callback()
      }
      return {
        title: 'Edit User',
        form: {
          id: '',
          name: '',
          email: '',
          head_img: ''
        },
        groupList: [],
        groups: [],
        rule: {
          name: [
            {validator: name, trigger: 'blur'}
          ],
          email: [
            {validator: email, trigger: 'blur'}
          ]
        },
        dialog_status: false,
        label_width: '120px'
      }
    },
    methods: {
      openDialog () {
        let that = this
        let dialog = {id: 'dialog_status', action: true}
        let load = this.message.load(this)
        apiPost(that, [], '/AuthGroup/getList', load, dialog, false).then(function (res) {
          that.groups = res.data
        })
      },
      closeDialog () {
        this.$refs['form'].resetFields()
        this.dialog_status = false
      },
      saveUser () {
        let that = this
        let dialog = {id: 'dialog_status', action: false}
        let load = this.message.load(this, 'Saving')
        apiPost(that, {id: that.form.id, groups: that.groupList}, '/User/setGroup', load, dialog).then(function (res) {
          if (res.code === 200) {
            that.$emit('refreshUsers')
            that.groupList = []
          }
        })
      }
    }
  }
</script>



