<template>
  <div>
    <div class="tool_bar">
      <el-button type="success" size="small" icon="fa fa-plus" @click="openAddGroup" :loading="loading.button">&nbsp;&nbsp;Add</el-button>
    </div>
    <addGroup ref="addGroup" @refreshTable="getTable"></addGroup>
    <div style="float: left; width: 48%">
      <el-table
        :data="tableData"
        style="width: 100%"
        :row-class-name="tableRowClassName">
        <el-table-column
          prop="title"
          label="组名称"
          width="160">
        </el-table-column>
        <el-table-column
          prop="description"
          label="描述">
        </el-table-column>
        <el-table-column label="操作" width="240">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="primary" size="mini" icon="el-icon-search"></el-button>
            <el-button @click="editClick(scope.row)" type="primary" size="mini" icon="el-icon-edit"></el-button>
            <el-button v-if="scope.row.status === 1" @click="stopClick(scope.row,2)" type="warning" size="mini" icon="fas fa-stop"></el-button>
            <el-button v-if="scope.row.status === 2" @click="stopClick(scope.row,1)" type="success" size="mini" icon="fas fa-play"></el-button>
            <el-button @click="delClick(scope.row)" type="danger" size="mini" icon="fas fa-trash-alt"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="tree_box" style="width: 40%">
      <el-tree
        :data="data"
        :props="defaultProps"
        highlightCurrent
        defaultExpandAll
        showCheckbox
        nodeKey="id"
        ref="tree"
        accordion>
      </el-tree>
    </div>
  </div>
</template>

<style scoped>
  .tool_bar {
    margin-top: -20px;
    margin-bottom: 10px;
  }

  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }

  .tree_box {
    float: left;
    margin-left: 3%;
    /*height: 10%;*/
    height: 30%;
    width: 80%;
  }
</style>

<script>
  import addGroup from './add'
  import { apiPost } from '../../static/js/common.js'

  export default {
    data () {
      return {
        activeName: '1',
        group: [],
        data: [],
        defaultProps: {
          children: 'second',
          label: 'first'
        },
        loading: {
          button: false
        },
        tableData: []
      }
    },
    methods: {
      openAddGroup () {
        this.loading.button = true
        this.$refs.addGroup.form = {id: '', title: '', description: '', rules: ''}
        this.$refs.addGroup.openAdd()
        setTimeout(() => {
          this.loading.button = false
        }, 3000)
      },
      tableRowClassName ({row, rowIndex}) {
        if (rowIndex === 1) {
          return 'warning-row'
        } else if (rowIndex === 3) {
          return 'success-row'
        }
        return ''
      },
      // 查看详细权限
      handleClick (row) {
        let keys = row.rules.split(',')
        this.$refs.tree.setCheckedKeys(keys)
      },

      // 编辑权限
      editClick (row) {
        this.$refs.addGroup.form = {id: row.id, title: row.title, description: row.description, rules: row.rules}
        this.$refs.addGroup.openAdd()
      },
      // 停用权限
      stopClick (row, status) {
        let that = this
        let load = this.message.load(this, 'Updating')
        apiPost(that, {id: row.id, status: status}, '/AuthGroup/setStatus', load).then(function () {
          that.getTable()
        })
      },
      // 删除权限组
      delClick (row) {
        let that = this
        let load = this.message.load(this, 'Updating')
        apiPost(that, {id: row.id}, '/AuthGroup/del', load).then(function () {
          that.getTable()
        })
      },
      // 获取子级
      getSecond (data) {
        let second = []
        let children = []
        for (let item in data) {
          if (data[item].second) {
            children = this.getSecond(data[item].second)
          } else {
            children = []
          }
          second.push({first: data[item].first + '  ' + data[item].description, id: data[item].id, second: children})
        }
        return second
      },
      getTable () {
        let that = this
        let load = this.message.load(this, 'Loading....')
        apiPost(that, '', '/AuthGroup/index', load, false, false).then(function (res) {
          that.tableData = res.data.group
        })
      }
    },
    created () {
      this.getTable()
      let that = this
      let load = this.message.load(this, 'Loading....')
      let dialog = {id: 'dialog_status', action: true}
      apiPost(that, '', '/AuthRule/index', load, dialog, false).then(function (res) {
        let data = []
        let second = []
        if (res.data) {
          for (let item in res.data) {
            let temp = res.data[item]
            if (temp.second) {
              second = that.getSecond(temp.second)
              data.push({first: temp.first + '  ' + temp.description, id: temp.id, second: second})
            } else {
              data.push({first: temp.first + '  ' + temp.description, id: temp.id})
            }
          }
        }
        that.$refs.addGroup.data = data
        that.data = data
      })
    },
    components: {
      addGroup: addGroup
    }
  }
</script>



