<template>
  <div>
    <div class="tool_bar">
      <el-button type="success" size="small" icon="fa fa-plus" @click="openAddRule" :loading="loading.add">&nbsp;&nbsp;Add</el-button>
      <el-button type="primary" size="small" icon="fa fa-edit" @click="editRule" :loading="loading.edit">&nbsp;&nbsp;Edit</el-button>
      <el-button v-if="this.status === 1" type="warning" size="small" icon="fa fa-stop" @click="stopRule(2)" :loading="loading.stop">&nbsp;&nbsp;stop
      </el-button>
      <el-button v-if="this.status === 2" type="success" size="small" icon="fa fa-play" @click="stopRule(1)" :loading="loading.stop">&nbsp;&nbsp;start
      </el-button>
      <el-button type="danger" size="small" icon="fa fa-trash-alt" @click="delRule">&nbsp;&nbsp;del</el-button>
    </div>
    <addRule ref="addRule" @refreshTree="getTree"></addRule>
    <p>AuthRule</p>
    <div class="tree_box">
      <el-tree
        :data="data"
        :props="defaultProps"
        accordion
        @node-click="handleNodeClick">
      </el-tree>
    </div>
  </div>
</template>


<style scoped>
  .tool_bar {
    margin-top: -20px;
    margin-bottom: 10px;
  }

  .tree_box {
    height: 30%;
    width: 30%;
  }
</style>

<script>
  import addRule from './add'
  import { apiPost } from '../../static/js/common.js'

  export default {
    data () {
      return {
        addDialog: false,
        data: [],
        defaultProps: {
          children: 'second',
          label: 'first'
        },
        loading: {
          add: false,
          edit: false
        },
        edit_id: '',
        status: 1
      }
    },
    methods: {
      handleNodeClick (data) {
        this.edit_id = data.id
        this.status = data.status
      },
      openAddRule () {
        this.loading.add = true
        this.$refs.addRule.form = {
          id: '',
          rule_id: '',
          menu_type: '1',
          title: '',
          icon: '',
          type: '2',
          name: '',
          sort: 1
        }
        this.$refs.addRule.openAdd()
        setTimeout(() => {
          this.loading.add = false
        }, 3000)
      },
      // 编辑权限
      editRule () {
        if (!this.edit_id) return this.message.topWarning(this, '请选择需要修改的权限')
        let that = this
        that.loading.edit = true
        let load = this.message.load(this)
        apiPost(that, {id: that.edit_id}, '/AuthRule/info', load, false, false).then(function (res) {
          that.$refs.addRule.form = res.data
          that.loading.edit = false
          that.$refs.addRule.openAdd()
        })
      },
      // 停用权限
      stopRule (status) {
        if (!this.edit_id) return this.message.topWarning(this, '请选择需要停用的权限')
        let that = this
        let load = this.message.load(this, 'Updating')
        apiPost(that, {id: that.edit_id, status: status}, '/AuthRule/setStatus', load, false).then(function () {
          that.getTree()
        })
      },
      // 删除权限
      delRule () {
        if (!this.edit_id) return this.message.topWarning(this, '请选择需要删除的权限')
        let that = this
        let load = this.message.load(this, 'Deleting')
        apiPost(that, {id: that.edit_id}, '/AuthRule/del', load, false).then(function () {
          that.getTree()
        })
      },
      // 获取树形图
      getTree () {
        let that = this
        let load = this.message.load(this, 'Loading....')
        apiPost(that, '', '/AuthRule/index', load, false, false).then(function (res) {
          let data = []
          let second = []
          if (res.data) {
            for (let item in res.data) {
              let temp = res.data[item]
              if (temp.second) {
                second = that.getSecond(temp.second)
                data.push({first: temp.first + '  ' + temp.description, id: temp.id, status: temp.status, second: second})
              } else {
                data.push({first: temp.first + '  ' + temp.description, id: temp.id, status: temp.status})
              }
            }
          }
          that.data = data
        })
      },
      // 获取子级
      getSecond (data) {
        let second = []
        let children = []
        for (let item in data) {
          if (data[item].second) {
            children = this.getSecond(data[item].second)
          } else {
            children = []
          }
          second.push({first: data[item].first + '  ' + data[item].description, id: data[item].id, status: data[item].status, second: children})
        }
        return second
      }
    },
    created () {
      this.getTree()
    },
    components: {
      addRule: addRule
    }
  }
</script>



